@charset "UTF-8";

.nav {
  z-index: 999;
  position: fixed;
  right: 0;
  width: em(170);
  height: 100vh;
  margin: auto;
  &__container {
    // transform: translate(110%, 0%);
    transition: all 1s;
    transform: translate(0%) !important;
    &--active {
    }
  }
  &__trigger {
    position: relative;
    display: block;
    width: em(100);
    height: em(100);
    border-radius: 100%;
    border: em(4) solid $black;
    margin: em(56) 0 em(160);
    background-color: $white;
    span {
      position: absolute;
      display: block;
      width: em(50);
      height: em(5);
      border-radius: em(5);
      background-color: $black;
      transition: all 0.5s;
      &:first-of-type {
        top: 50%;
        left: 50%;
        transform: translate(-50%, em(-10));
      }
      &:nth-of-type(2) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, em(10));
      }
    }
    &--active {
      span {
        &:first-of-type {
          transform: translate(-50%, em(0)) rotate(20deg);
        }
        &:nth-of-type(2) {
          transform: translate(-50%, em(0)) rotate(-20deg);
        }
      }
    }
  }
  &__link {
    position: relative;
    display: block;
    text-align: left;
    padding: em(32) em(0) em(32) em(28);
    p {
      font-size: $f__18;
    }
    span {
      position: absolute;
      display: block;
      top: em(39);
      left: em(0);
      width: em(12);
      height: em(12);
      background: url('../../assets/buttons/btn-menu.png') no-repeat center/100%;
    }
    &--active {
      span {
        width: em(24);
        height: em(24);
        left: em(-6);
        background: url('../../assets/buttons/btn-menu-active.png') no-repeat
          center/100%;
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .nav {
    font-size: 0.8em;
  }
}

@media screen and (max-width: $pad-width) {
  .nav {
    font-size: 0.6em;
  }
}

@media screen and (max-width: $desktop-width) {
  .nav {
    width: em(80);
    height: em(80);
    font-size: 1em;
    &__container {
      position: fixed;
      justify-content: center;
      left: 0;
      bottom: 0;
      width: 100%;
      display: flex;
      transform: translate(0%, 0%);
      background-color: $white;
      border-top: em(5) solid $black;
    }
    &__trigger {
      font-size: 0.6em;
      padding: em(30);
    }
    &__link {
      padding: em(50) em(32) em(14);
      span {
        top: em(16);
        left: 40%;
        transform: rotate(90deg);
      }
    }
  }
}
