.floor1 {
  .info {
    &__container {
      background: url('../../assets/pages/floor1/floor1-info.png') no-repeat
        center/ 100%;
    }
  }
  .floor {
    &__wrapper {
      background: url('../../assets/pages/floor1/floor1-bg.png') no-repeat right
        bottom/auto 100%;
    }
    &__question {
      &::after {
        background: url('../../assets/pages/floor1/origin/floor1-bubble.png')
          no-repeat center/100%;
      }
      &--active,
      &:hover {
        &::after {
          background: url('../../assets/pages/floor1/active/floor1-bubble--active.png')
            no-repeat center/100%;
        }
      }
      &--1 {
        top: 56%;
        right: 26%;
        img {
          width: em(240);
        }
      }
      &--2 {
        top: 20%;
        right: 22%;
        img {
          width: em(350);
        }
      }
      &--3 {
        top: 26%;
        left: 41%;
        img {
          width: em(315);
        }
        &::after {
          left: em(-50);
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .floor1 {
  }
}

@media screen and (max-width: $pad-width) {
  .floor1 {
  }
}
@media screen and (max-width: $desktop-width) {
  .floor1 {
    .info {
      &__container {
        background: url('../../assets/pages/floor1/floor1-info-m.png') no-repeat
          top/ 100%;
      }
    }
    .floor {
      &__wrapper {
        background: url('../../assets/pages/floor1/floor1-bg-m.png') no-repeat
          right em(100) / 100%;
      }
      &__question {
        &--1 {
          z-index: 1;
          top: em(300);
          left: 60%;
          img {
            width: em(150);
          }
        }
        &--2 {
          top: em(140);
          left: 65%;
          img {
            width: em(200);
          }
        }
        &--3 {
          top: em(180);
          left: 30%;
          img {
            width: em(200);
          }
        }
      }
    }
  }
}
